import {
    isvalidPhone
} from '@/utils/validate'

export default {
    data() {
        return {
            codeTxt: '',
            codeTime: 60,
            codeTimer: null,
            canclick: true
        }
    },
    destroyed() {
        this.clearCodeTimer()
    },
    methods: {
        clearCodeTimer() {
            this.codeTime = 60
            this.codeTxt = ``
            clearInterval(this.codeTimer)
            this.canclick = true
        },
        // 
        sendCode(data) {
            //  sendCode(data)
            if (!isvalidPhone(data)) {
                console.log('')
            } else {
                if(this.canclick){
                    this.codeTxt = `${--this.codeTime}`
                    this.codeTimer = setInterval(() => {
                        if (this.codeTime > 0) {
                            this.codeTxt = `${--this.codeTime}`
                            this.canclick = false
                        } else {
                            this.clearCodeTimer()

                        }

                    }, 1000)
                }
               
            }

        },
        // 
        isCode(rule, data, callback) {
            //const res = await check_code(data)
            //res.data.success ? callback() : callback(new Error(res.data.msg))
        }

    }
}